export default {
    methods: {
        /**
         * @param {String} modalContentName Name of modal content component
         */
        openModal(modalContentName) {
            if (!modalContentName) return;

            this.$store.dispatch('modal/openModal', {
                content: modalContentName,
            });
        },

        closeModal() {
            this.$store.dispatch('modal/closeModal');
        },
    },
};
